import { Component, OnInit } from '@angular/core';
import { PostsService } from '../services/posts.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-single-post',
  templateUrl: './single-post.component.html',
  styleUrls: ['./single-post.component.css', '../../assets/css/style.css']
})
export class SinglePostComponent implements OnInit {
  posts=[];
  errorMsg: null;
  id: string;
  category: string;
  constructor(private _postsService: PostsService, private _route:ActivatedRoute) { }

  ngOnInit(): void {
    this.category = this._route.snapshot.paramMap.get('category');
    this.id=this._route.snapshot.paramMap.get('id');
    this._postsService.getPost(this.category,this.id)
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
  }

}
