import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { tap, catchError } from 'rxjs/operators';
import { throwError as observableThrowError, Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class PostsService {
  _url: string = "/";

  constructor(private _http: HttpClient) { }
  //All posts
  getPosts(category:string): Observable<any[]> {
    return this._http.get<any[]>(this._url +'api/'+ category)
      .pipe(tap(data => console.log(data)), catchError(this.errorHandler))
  }
  //Single Post
  getPost(category: string,id:string): Observable<any[]> {
    return this._http.get<any>(this._url + category + '/' +id )
      .pipe(tap(data => console.log(data)), catchError(this.errorHandler))
  }
  //Delete Post
  deletePost(category: string, id: string): Observable<any[]> {
    return this._http.delete<any>(this._url +'deletepost/'+ category + '/' + id)
      .pipe(tap(data => console.log(data)), catchError(this.errorHandler))
  }
  //Add Post
  addPost(body){
    console.log(body)
    return this._http.post<any>(this._url+'addpost', body)
      .pipe(tap(data => console.log(data)), catchError(this.errorHandler))

  }

  //Edit Post
  editPost(body,id:string) {
    console.log(body)
    return this._http.post<any>(this._url +'editpost/'+id, body)
      .pipe(tap(data => console.log(data)), catchError(this.errorHandler))

  }
  errorHandler(error: HttpErrorResponse) {
    //GIVE WRONG URL / WRONG ADDRESS TO SEE ERROR MESSAGE
    return observableThrowError(error.message || "Server Error");
  }

}
