import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { PostsService } from '../services/posts.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-edit-post',
  templateUrl: './edit-post.component.html',
  styleUrls: ['./edit-post.component.css', '../../assets/css/style.css']
})
export class EditPostComponent implements OnInit,AfterViewInit {

  public Editor = ClassicEditor;
  constructor(private _http: HttpClient, private _postsService: PostsService, private _route: ActivatedRoute,private _router:Router) { }
  selectedImage: File = null;
  errorMsg: null;

  //for route params
  id: string;
  category: string;
  //Two Way Data Binding Data
  formTitle:string;
  formDescription:String;
  formCategory:string;
  formImageUrl:string;


  ngOnInit(): void {
    this.category = this._route.snapshot.paramMap.get('category');
    this.id = this._route.snapshot.paramMap.get('id');
    this._postsService.getPost(this.category, this.id)
      .subscribe(data => {
        this.formTitle = data[0].title;
        this.formDescription = data[0].description;
        this.formCategory = data[0].category;
        this.formImageUrl = data[0].imageUrl;
      },
        error => this.errorMsg = error);

  }
  ngAfterViewInit(){

  }
  onSubmit(form) {
    let url = 'http://localhost:5000/editpost/'+this.id
    let body = {
      title: this.formTitle,
      description: this.formDescription,
      category: this.formCategory,
      imageUrl: this.formImageUrl
    };
    console.log(body);

    this._postsService.editPost(body,this.id)
      .subscribe(data => console.log(data),
        error => this.errorMsg = error);

    this._router.navigate([this.category]);
    //resetting Form
    // this.addpostRef.resetForm()
    // this.selectedImageUrl = null;
  }
  selectImage(e) {
    this.selectedImage = e.target.files[0];
  }
  uploadImage(e) {
    e.preventDefault();
    let url = 'https://api.cloudinary.com/v1_1/djcy3b8pn/image/upload'
    const fd = new FormData();
    fd.append('file', this.selectedImage)
    fd.append('upload_preset', 'Agri-World')
    fd.append('cloud_name', 'djcy3b8pn')
    this._http.post<any>(url, fd).subscribe(
      data => {
        console.log(data.secure_url);
        console.log(data.url)
        this.formImageUrl = data.url

      },
      error => console.log(error)
    )

  }
}
