import { Component, OnInit } from '@angular/core';
import { PostsService } from 'src/app/services/posts.service';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { Router } from '@angular/router';
@Component({
  selector: 'app-problems',
  templateUrl: './problems.component.html',
  styleUrls: ['./problems.component.css', '../../../assets/css/style.css']
})
export class ProblemsComponent implements OnInit {
  posts = [];
  errorMsg: null;

  constructor(private _postsService: PostsService, private router: Router) { }

  ngOnInit() {
    this._postsService.getPosts('problems')
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
  }
  openPost(id) {
    console.log(id)
    this.router.navigate(['singlepost', 'problems', id])
  }
  deletePost(id) {
    console.log(id)
    this._postsService.deletePost('problems', id)
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
    //Refresh page without Reloading
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate(['problems']);
    });
  }
  editPost(id) {
    console.log(id)
    this.router.navigate(['editpost', 'problems', id])
  }
}
