import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { Router } from '@angular/router';
import { PostsService } from '../services/posts.service';

@Component({
  selector: 'app-add-post',
  templateUrl: './add-post.component.html',
  styleUrls: ['./add-post.component.css', '../../assets/css/style.css']
})
export class AddPostComponent implements OnInit {
  public Editor = ClassicEditor;
  errorMsg: null;
  @ViewChild('addpost') addpostRef;
  constructor(private _http: HttpClient, private _router: Router, private _postsService: PostsService) { }
  selectedImage:File=null;
  selectedImageUrl=null;
  ngOnInit(): void {
  }
  onSubmit(){
  let body = {
      title: this.addpostRef.value.title,
      description: this.addpostRef.value.description,
      category: this.addpostRef.value.category,
      imageUrl: this.selectedImageUrl
    }
  //console.log(body)

  this._postsService.addPost(body)
      .subscribe(data => console.log(data),
        error => this.errorMsg = error);

  //resetting Form
  this.addpostRef.resetForm()
  this.selectedImageUrl = null;
  }
  selectImage(e){
    this.selectedImage=e.target.files[0];
  }
  uploadImage(e){
    e.preventDefault();
    let url ='https://api.cloudinary.com/v1_1/djcy3b8pn/image/upload'
    const fd =new FormData();
    fd.append('file',this.selectedImage)
    fd.append('upload_preset', 'Agri-World')
    fd.append('cloud_name','djcy3b8pn')
    this._http.post<any>(url, fd).subscribe(
      data => {
        console.log(data.secure_url);
        console.log(data.url)
        this.selectedImageUrl=data.url

      },
      error => console.log(error)
    )

  }
}
