import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PostsService } from 'src/app/services/posts.service';

@Component({
  selector: 'app-farming-methods',
  templateUrl: './farming-methods.component.html',
  styleUrls: ['./farming-methods.component.css', '../../../assets/css/style.css']
})
export class FarmingMethodsComponent implements OnInit {
  posts = [];
  errorMsg: null;

  constructor(private _postsService: PostsService, private router: Router) { }

  ngOnInit() {
    this._postsService.getPosts('farming_methods')
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
  }
  openPost(id) {
    console.log(id)
    this.router.navigate(['singlepost', 'farming_methods', id])
  }
  deletePost(id) {
    console.log(id)
    this._postsService.deletePost('farming_methods', id)
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
    //Refresh page without Reloading
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate(['farming_methods']);
    });
  }
  editPost(id) {
    console.log(id)
    this.router.navigate(['editpost', 'farming_methods', id])
  }
}
