import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PostsService } from 'src/app/services/posts.service';
@Component({
  selector: 'app-technology',
  templateUrl: './technology.component.html',
  styleUrls: ['./technology.component.css', '../../../assets/css/style.css']
})
export class TechnologyComponent implements OnInit {
  posts = [];
  errorMsg: null;

  constructor(private _postsService: PostsService, private router: Router) { }

  ngOnInit() {
    this._postsService.getPosts('technology')
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
  }
  openPost(id) {
    console.log(id)
    this.router.navigate(['singlepost', 'technology', id])
  }
  deletePost(id) {
    console.log(id)
    this._postsService.deletePost('technology', id)
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
    //Refresh page without Reloading
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate(['technology']);
    });
  }
  editPost(id) {
    console.log(id)
    this.router.navigate(['editpost', 'technology', id])
  }
}
