import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TechnologyComponent } from './Categories/technology/technology.component';
import { FarmingMethodsComponent } from './Categories/farming-methods/farming-methods.component';
import { ProblemsComponent } from './Categories/problems/problems.component';
import { HealthComponent } from './Categories/health/health.component';
import { HomeComponent } from './Categories/home/home.component';

import { AddPostComponent } from './add-post/add-post.component';
import { EditPostComponent } from './edit-post/edit-post.component';
import { SinglePostComponent } from './single-post/single-post.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'technology', component: TechnologyComponent },
  { path: 'farming_methods', component: FarmingMethodsComponent },
  { path: 'problems', component: ProblemsComponent },
  { path: 'health', component: HealthComponent },
  { path: 'addpost', component: AddPostComponent },
  { path: 'editpost/:category/:id', component: EditPostComponent },
  { path: 'singlepost/:category/:id', component: SinglePostComponent },
  { path: '**', component: HomeComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const RoutingComponents = [TechnologyComponent, FarmingMethodsComponent, ProblemsComponent, HealthComponent, HomeComponent, AddPostComponent, EditPostComponent, SinglePostComponent ]
