import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PostsService } from 'src/app/services/posts.service';

@Component({
  selector: 'app-health',
  templateUrl: './health.component.html',
  styleUrls: ['./health.component.css', '../../../assets/css/style.css']
})
export class HealthComponent implements OnInit {

  posts = [];
  errorMsg: null;

  constructor(private _postsService: PostsService, private router: Router) { }

  ngOnInit() {
    this._postsService.getPosts('health')
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
  }
  openPost(id){
    console.log(id)
    this.router.navigate(['singlepost','health',id])
  }
  deletePost(id){
    console.log(id)
    this._postsService.deletePost('health', id)
      .subscribe(data => this.posts = data,
        error => this.errorMsg = error);
    //Refresh page without Reloading
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate(['health']);
    });
  }
  editPost(id){
    console.log(id)
    this.router.navigate(['editpost', 'health', id])
  }
}
